.body {
  background-color: black;
  width: calc(45vw - 40px);
  box-shadow: 0px 0px 24px 8px #000000;
  border-radius: 10px;
}

.card {
  display: flex;
  color: #dddddd;
}

.card img {
  overflow: hidden;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.card div {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-self: center;
}

.card div h2 {
  font-size: 1.5rem;
  max-width: 300px;
  margin: 0 auto;
}

.card div div {
  width: 300px;
  height: 100%;
}

.card div div h3 {
  font-size: 1rem;
}
