.loadingBox {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.title {
  color: red;
  border-bottom: 1px solid black;
  margin-bottom: 30px;
}

.title a {
  text-decoration: none;
  color: inherit;
}

.cardBox {
  display: flex;
  gap: 3rem 3rem;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

.body footer {
  margin-top: 20px;
  height: 40px;
  color: red;
  background: black;
  text-align: center;
  padding-top: 20px;
  font-size: 1.5rem;
}

.body footer a {
  text-decoration: none;
  color: inherit;
}

.body footer a:hover {
  text-decoration: underline;
}
